<template>
  <div class="seletor">
    <v-form ref="busca">
      <div class="header">
        <span class="search-title">Filtro de busca</span>
        <span class="search-subtitle">Escolha pelo menos um filtro</span>
      </div>
<v-divider></v-divider>
      <div class="input">
        <v-select dark label="selecione uma categoria" v-model="selected" :items="categorias" item-value="value" item-text="name"/>
        
        <v-btn dark color="blue-grey darken-4" v-on:click="(buscar(selected))">Buscar</v-btn>
      </div>
    </v-form>
  </div>
</template>
<script>
export default {
  name: "SearchForm",
  data() {
    return {
      input: "",
      opcao1: true,
      selected: '',
      categorias: [
        { name: "educação", value: "EDUCACAO" },
        { name: "lazer", value: "LAZER" },
        { name: "SAUDE", value: "SAUDE" },
        { name: "FESTIVIDADES", value: "FESTIVIDADES" },
      
      ],
      
      localizacao: { centro: "centro", bairro: "bairro" },
    };
    
  },
  methods: {
    buscar( ){
      this.$emit('buscar-por',{selected: this.selected});
    }
  }
};
</script>
<style lang="scss" scoped>
